import React, {Component} from 'react';
import './App.css';
import PropTypes from "prop-types";


export default class Topic extends Component {
    static propTypes = {
        headers: PropTypes.arrayOf(PropTypes.string),
        children: PropTypes.any.isRequired,
    };

    constructor() {
        super();
        this.state = {

        }
        if (process.env.NODE_ENV === 'development') {
            this.state = {
                hidden: false
            };
        }

    }


    render() {
        let headline = "";
        if (this.props.headers !== undefined) {
            headline = this.props.headers.length > 1 ? this.props.headers.join("  ") : this.props.headers;
        }
        let hidden = this.state.hidden;
        let is_local = process.env.NODE_ENV === 'development';
        if(is_local && hidden){
            return null
        }

        return (<div className="topic-container">
            {is_local && <div className="xbutton" onClick={() => {this.setState({hidden:true})}}/>}
            <div className="headline">{headline}</div>
            <div className="description">
                {this.props.children}
            </div>
        </div>)
    }
}


