import React, {Component} from 'react';
import './App.css';
import './PrintApp.css';
import HeaderBar from './HeaderBar'
import Section from "./Section";
import Topic from "./Topic";
import {basic_information, contacts, experience, referees, technologies, theme} from "./Resume";
class App extends Component {


    constructor() {
        super();
        this.state = {
            theme: theme,
            techExpanded: undefined
        };
    }

    updateTheme = (newTheme) => {
        this.setState({
            theme: newTheme
        })
    };

    expandTech = (uniqueID) => {
        if (this.state.techExpanded !== uniqueID) {
            this.setState({
                techExpanded: uniqueID
            })
        } else {
            this.hideTech()
        }
    };
    hideTech = () => {
        this.setState({
            techExpanded: undefined
        })
    };

    render() {
        const pseudobuttonstyle = {
            background: theme.foreground,
            borderColor: theme.foregroundDarker,
            boxShadow: theme.foreground,
        };
        return (
            <div id="resume-container">
                <HeaderBar
                    name={basic_information.name}
                    mobile={basic_information.phone}
                    email={basic_information.email}
                />
                <div className="print-table">
                    <div id="sidebar">
                        <Section title={"Objective"}>
                            <Topic>{basic_information.objective}</Topic>
                        </Section>
                        <Section title={"Technologies"}>
                            <Topic>
                                {technologies.map((x) => {
                                    let showLong = this.state.techExpanded === x.short;
                                    return (
                                        <div key={x.short} className='techItem'
                                             onClick={() => this.expandTech(x.short)}>
                                            {showLong ? x.long : x.short}
                                            {showLong &&
                                            <ul className='techsublist'>
                                                {x.frameworks.map((y) => {
                                                    return (
                                                        <li key={y}>{y}</li>
                                                    )
                                                })}
                                            </ul>
                                            }
                                        </div>
                                    )
                                })}
                            </Topic>
                        </Section>
                        <Section title={"Find Me"}>
                            <Topic>
                                {contacts.map((v) => {
                                    return <div key={v.url}>
                                        {v.icon}
                                        <a
                                            href={v.url}>{v.url}
                                        </a>
                                    </div>
                                })}
                            </Topic>
                        </Section>
                        <Section title={"References"}>
                            {process.env.NODE_ENV === 'development' ?
                                referees.map((r) => {
                                    return <Topic>
                                        <div><b>{r.name}</b></div>
                                        <div><i>{r.role}</i></div>
                                        <div className="contact-field">
                                        {Object.keys(r.contacts).map((contact_type) => {
                                            return <div><b>{contact_type}:</b> {r.contacts[contact_type]}</div>
                                        })}
                                        </div>
                                    </Topic>
                                })
                                :
                                <Topic>
                                    <div> {basic_information.reference_policy} </div>
                                </Topic>
                            }
                        </Section>
                    </div>


                    <div id="contentbar">
                        {
                            Object.keys(experience).map((key) => {
                                return <Section title={key} key={key}>
                                    {experience[key].map((role) => <Topic headers={role.headline}
                                                                          key={role.headline.join("")}>
                                            {role.short}
                                            <div style={{marginTop: '5px'}}>
                                                {role.link !== undefined &&
                                                <span><a href={role.link} style={pseudobuttonstyle} className="pseudo-button">Read More</a></span>}
                                            </div>
                                        </Topic>
                                    )}
                                </Section>
                            })
                        }
                    </div>
                </div>
            </div>

        )
    }
}

export default App;



