import React, {Component} from 'react';
import './App.css';
import {theme} from './Resume'
import PropTypes from "prop-types";

class HeaderBar extends Component {
    static propTypes = {
        name: PropTypes.string.isRequired,
        mobile: PropTypes.string,
        email: PropTypes.string,
    };

    getInitials() {
        let values = this.props.name.split(" ");
        let firstname = values[0];
        let lastname = values[values.length - 1];
        return firstname[0] + lastname[0];
    }

    render() {
        const vSize = 200;
        const outerBubble = {background: theme.foreground, width: vSize, height: vSize};
        const innerBubble = {background: theme.page, width: '98%', height: '98%', margin: 'auto'};
        const innerText = {margin: 'auto', color: theme.foreground};
        const outerBox = {background: theme.foreground, height: vSize / 2};

        let {name, mobile, email} = this.props;
        let initials = this.getInitials();
        return (
            <div className="header">
                <div className="headerBox" style={outerBox}>
                    <div className="headerText">
                        <div className="header_name">{name}</div>
                        <div className="header_other">
                            {mobile !== undefined && [<a className={"ignore"} href={"tel:" + mobile}><img className='headerIcon' src={'icons/phone.svg'} alt="phone"/>{mobile}</a>, " | "]}
                            {email !== undefined && <a className={"ignore"} href={"mailto:" + email}><img className='headerIcon' src={'icons/envelope.svg'} alt="email"/>{email}</a>}
                        </div>
                    </div>
                </div>
                <div className="headerCircle" style={outerBubble}>
                    <div className="headerCircle" style={innerBubble}>
                        <p className={"ft0"} style={innerText}>{initials}</p>
                    </div>
                </div>
            </div>)
    }
}

export default HeaderBar


